a.underline-link::before, a.underline-link::after, a.blue-link::before, a.blue-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
}
a.underline-link::before, a.blue-link::before {
  transition: all 0s linear 0.3s;
  left: 0;
  width: 0;
}
a.underline-link::after, a.blue-link::after {
  transition: all 0s linear 0s;
  right: 0;
  width: 100%;
}

a.underline-link {
  color: #000000;
}
