.quadrants__block {
  width: calc(100% + 1px);
  padding-bottom: calc(100% + 1px);
  background: black;
  transition: transform 0.4s linear, opacity 0.3s, box-shadow 0.3s;
  cursor: pointer;
  background: url("../images/blocks/hero/home-hero.jpg") no-repeat;
  background-size: 1200% 400%;
}
.quadrants__block--1 {
  border-radius: 0 100% 0 0;
}
.quadrants__block--2 {
  border-radius: 0 0 100% 0;
}
.quadrants__block--3 {
  border-radius: 0 0 0 100%;
}
.quadrants__block--4 {
  border-radius: 100% 0 0 0;
}
@media screen and (min-width: 751px) {
  .quadrants__block:hover {
    transition: transform 0.4s linear, opacity 0.6s, box-shadow 0.3s;
    opacity: 0.6 !important;
  }
}
.quadrants__block.active {
  position: relative;
  z-index: 2;
  opacity: 1 !important;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3);
}
.quadrants__block.active.shadow-90 {
  box-shadow: 4px 0 4px rgba(0, 0, 0, 0.3);
}
.quadrants__block.active.shadow-180 {
  box-shadow: 0 -4px 4px rgba(0, 0, 0, 0.3);
}
.quadrants__block.active.shadow-270 {
  box-shadow: -4px 0 4px rgba(0, 0, 0, 0.3);
}
.quadrants__col {
  width: 8.3333333333%;
}
.quadrants__col:nth-child(1) .quadrants__block:nth-child(1) {
  background-position: calc(0%) 0;
}
.quadrants__col:nth-child(1) .quadrants__block:nth-child(2) {
  background-position: calc(0%) 33.3333333333%;
}
.quadrants__col:nth-child(1) .quadrants__block:nth-child(3) {
  background-position: calc(0%) 66.6666666667%;
}
.quadrants__col:nth-child(1) .quadrants__block:nth-child(4) {
  background-position: calc(0%) 100%;
}
.quadrants__col:nth-child(2) .quadrants__block:nth-child(1) {
  background-position: calc(9.0909090909%) 0;
}
.quadrants__col:nth-child(2) .quadrants__block:nth-child(2) {
  background-position: calc(9.0909090909%) 33.3333333333%;
}
.quadrants__col:nth-child(2) .quadrants__block:nth-child(3) {
  background-position: calc(9.0909090909%) 66.6666666667%;
}
.quadrants__col:nth-child(2) .quadrants__block:nth-child(4) {
